import { Component, ElementRef, ViewChild, OnInit } from '@angular/core';
import { fromEvent } from 'rxjs';
import { map, debounceTime, distinctUntilChanged, tap } from 'rxjs/operators';
import { DataService } from '../data-service.service';
import { Plant } from '../models';
import { ActivatedRoute } from '@angular/router';

// tslint:disable-next-line: ban-types
declare let gtag: Function;

@Component({
  selector: 'app-plants-list',
  templateUrl: './plants-list.component.html',
  styleUrls: ['./plants-list.component.scss']
})
export class PlantsListComponent implements OnInit {
  @ViewChild('searchBox', { static: true }) searchBox: ElementRef;
  searchboxChangeSubscription: any;
  plants: Plant[];
  searchText: string;
  currentPage: number = 1;
  totalPages: number;

  constructor(private dataService: DataService,
    private route: ActivatedRoute) { 
      this.route.paramMap.subscribe((paramsMap: any) => {
        console.log(paramsMap);
        if(paramsMap.params.id){
          this.navigateToPlant(paramsMap, 'link', 'link')
        }
      });
    }

  get hasMorePages() {
    return this.totalPages > this.currentPage;
  }


  ngOnInit(): void {
    this.searchboxChangeSubscription = fromEvent(this.searchBox.nativeElement, 'keyup')
      .pipe(
        map(() => this.searchBox.nativeElement.value),
        debounceTime(350),
        distinctUntilChanged(),
        tap(searchTerm => this.search(searchTerm))
      )
      .subscribe();
    this.search('');
  }

  private search(searchTerm: string) {
    this.sendSearchAnalEvent(searchTerm);
    this.currentPage = 1;

    return this.dataService.getPlants(this.currentPage, searchTerm)
      .subscribe(result => {
        this.totalPages = result.totalPages;
        this.plants = result.plants;
      });
  }

  clearSearch() {
    this.searchBox.nativeElement.value = null;
    this.currentPage = 1;
    this.search(null);
    this.sendClearSearchAnalEvent();
  }

  navigateToPlant(target, source, relationship) {
    this.searchBox.nativeElement.value = target;
    this.search(target);
    this.sendClickPlantLinkAnalEvent(source, target, relationship);
  }

  loadMore() {
    this.currentPage++;
    this.dataService.getPlants(this.currentPage, this.searchText)
    .subscribe(result => {
      this.plants.push(...result.plants);
    })
  }

  plantNameToUrl(name: string) {
    return '/'+ name;
  }

  private sendSearchAnalEvent(value: string) {
    gtag('send', 'event', {
      eventCategory: 'interaction',
      eventAction: 'search',
      eventValue: value
    });
  }

  private sendClearSearchAnalEvent() {
    gtag('send', 'event', {
      eventCategory: 'interaction',
      eventAction: 'clearSearch',
    });
  }

  private sendClickPlantLinkAnalEvent(from, to, relationship) {
    gtag('send', 'event', {
      eventCategory: 'interaction',
      eventAction: 'clickedOnPlantReference',
      eventValue: {
        from,
        to,
        relationship
      }
    });
  }

}
