import { Injectable, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Plant, GetPlantsResult } from './models';
import { shareReplay, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private data: Observable<Plant[]>;
  private defaultPageSize = 10;
  constructor(private http: HttpClient) {
    this.data = this.loadPlantsData().pipe(
      shareReplay(1)
    );
  }

  getPlants(page: number, searchTerm: string): Observable<GetPlantsResult> {
    let result = this.data;
    if (searchTerm) {
      result = result.pipe(
        map((plants: Plant[]) =>
          plants.filter(x => x.name.toLowerCase().startsWith(searchTerm.toLowerCase()))
        )
      );
    }

    return result.pipe(map((plants: Plant[]) => {
      const totalPages = Math.ceil(plants.length / this.defaultPageSize);

      const start = (page - 1) * 10;
      if (start >= plants.length) {
        return {
          plants: [],
          totalPages
        };
      }

      const stop = start + 10;

      return {
        plants: plants.slice(start, stop),
        totalPages
      };
    }
    ));
  }

  private loadPlantsData(): Observable<Plant[]> {
    return this.http.get<Plant[]>('./assets/plants.json');
  }
}
